* {
    box-sizing: border-box;
  }
  
  html {
    font-family: Roboto, sans-serif;
    font-weight: 400;
    font-size: 12px;
  }
  
  p,
  h1,
  h2,
  h3,
  body {
    margin: 0;
    padding: 0;
  }
  
  :root {
    --color-regular: #060b35;
    --color--blue: #00acdc;
    --color--white: #fff;
  }
  